<template>
   
    <div class="d-inline">

        <div class="tree-table-content"  :class="{ 'tree-root': the_item.index < 2 }">
            <div class="unid tree-col">
                {{ the_item.number_code }}
            </div>

            <div class="ah_empty tree-col text-left">
                {{ the_item.display_name }}
            </div>

            <div class="ipv tree-col">
                {{ the_item.index }}
            </div>
            <div class="ipv tree-col" v-html="the_item.ipv">
                
            </div>
            <div class="level tree-col">
                <vip-label :item="the_item"></vip-label>
            </div>
            
            <div class="result_xl tree-col  fs-15">
                {{ this.usd }}
            </div>
        </div>
    </div>
</template>

<script>
import VipLabel from '@/components/Tree/VipLabel.vue'

    export default {
        components: { VipLabel  },
        name: '',
        props: {
            the_item: {
                type: Object
            },
            allow: {
                type: Boolean
            }
        },
        name: '',
        data() {
            return {
                num: 0,
                vip: '5级会员',
                usd: 0,
                ipv: 0,

            }
        },
        async mounted() {
            this.usd = this.the_item.bonus.usd_active_house // await this.load_data()
        },
        computed: {
            num_color() {
                if (this.num > 0) {
                    return true
                }
                return false
            },
            
            /*
            show_pv() {
                if (this.the_item.vip) { return this.the_item.ipv }
                return '待计算'
            },
            show_son_pv() {
                if (this.the_item.vip) { return this.the_item.son_pv }
                return ''
            },
            */
           
            is_show() {
                let res = false
                if (this.the_item.vip) { res = true }
                return res
            }
        },
        methods: {
            /*
            async _numing() {
                return await bonus_active_house(this.the_item)
            },
            load_data() {
                const self = this
                return new Promise((rej, rev) => {
                    let res = 0

                    res = this._numing()

                    rej(res)
                })
            },
            */
        }
    }
</script>

<style lang="sass" scoped>
@import "@/common/sass/table_header.sass" 
.usd
    color: #607d8b!important
</style> 


